#section2 {
    width: 100%; 
    padding: 27.44px 0 0 0;
    .container {
        width: 100%;
        max-width: 1903px;
        position: relative;
        .gap {
            width: 100%;
            .content {
                width: 100%;
                display: flex;
                
                .left-img-box {
                    width: 100%;
                    height: 1106px;
                    position: relative;
                    ul {
                        width: 100%;
                        li {
                            width: 100%;
                           
                            a {
                                position: relative;
                                text-align: center;
                                
                                
                                img {
                                    position:absolute;
                                    z-index: 4;
                                    top:0;
                                    bottom: 0;
                                    width: 951.5px;
                                    height: 1105.55px;
                                    display: flex;
                                    justify-content: center;
                                    align-items: center;
                                    // transition: all 0.2s;
                                    // &:hover {
                                    //     transform: scale(0.9);
                                    // }
                                }
                                h2 {
                                    width: 192.61px;
                                    height: 18px;
                                    text-align: center;
                                   
                                    color: rgb(0,0,0);
                                    font-size: 16px;
                                }
                            } 
                            img {
                                position: absolute;
                                z-index: 5;
                                top: 0;
                                left: 0;
                                margin: 0 auto;
                                text-align: center;
                                width: 251.562px;
                                height: 27.828px;
                            }
                           
                        }
                    }
                }
                .right-img-box {
                    ul {
                        li {
                            text-align: center;
                            a {
                                img {
                                    width: 951.5px;
                                    height: 1105.55px;
                                }
                            }
                            img {
                                width: 210px;
                                height: 115px;
                                text-align: center;
                            }
                        }
                    }
                }
            }
        }
    }
}